@media only screen and (max-width : 600px) {
	div.jar {
		width:100%;
		padding:5%;
	}
}
@media only screen and (min-width : 600px) {
	div.jar {
		width:50%;
	}
}

img#logo {
	text-align:center;
	margin-bottom:3em;
}

span#intro { 
	margin:0 1em;
	text-align: center;
	display:inline-block
}
span.navigation {
	text-align:center;
	width:100%;
	display:inline-block;
}
span.navigation a {
	font-size: 1.05em;
	padding: 0 0.5em;
}

div.jar {
	figure {
		margin: 0 0 1em 0;
	}
	h4 {
		margin: 0 0 0.5em 0;
	}
	p {
		font-style:italic;
		margin: 0 1em;
		text-align:justify;
		hyphens: auto;
	}
}

h1 {
	font-family: Savoye LET, Roboto, -apple-system, BlinkMacSystemFont, "Segoe UI", Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	color: $header-color;
	font-weight: bold;
	margin-top:2em;
	text-align:center;
}
h2 {
	font-family: Savoye LET, Roboto, -apple-system, BlinkMacSystemFont, "Segoe UI", Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	color: $header-color;
	font-weight: bold;
	text-align:center;
}
h3 {
	font-family: Savoye LET, Roboto, -apple-system, BlinkMacSystemFont, "Segoe UI", Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	color: $header-color;
	font-weight: bold;
	text-align:center;
}
h4 {
	color: $header-color;
	text-align:center;
}
h5 {
	color: $header-color;
}
p.text {
	text-align:left;
}

div#osm-map {
	height: 350px;
}

div.jar {
	display:inline-block;
	float:left;
}

div.usage {
	display:inline-block;
	float:left;
	width:33.33333%;
	padding:0 6%;
	text-align:center;
}

li { margin-bottom: 0 }
ul { 
	list-style: inside;
	text-indent: -1em;
	padding-left: 1em;
}
